<template>
  <div class="container-publish">
    <el-card>
      <div slot="header">
        <my-bread>{{$route.query.id ?'' :''}}</my-bread>
      </div>
      <!--  -->
      <el-form label-width="80px" :model="articleForm" :rules="articleRules" ref="articleForm">
        <el-form-item label=": " prop="title">
          <el-input v-model="articleForm.title" placeholder="" style="width:400px"></el-input>
        </el-form-item>
        <el-form-item label=": " prop="content">
          <!--   
          ref="myQuillEditor"
          @blur="onEditorBlur($event)"
          @focus="onEditorFocus($event)"
          @ready="onEditorReady($event)"
          editorOption-->
          <quill-editor
            v-model="articleForm.content"
            :options="editorOption"
            @blur="checkContemt()"/>
        </el-form-item>
        <el-form-item label=": " prop="cover.type">
          <!--  -->
          <!-- @change="articleForm.cover.images=[] -->
          <el-radio-group v-model="articleForm.cover.type" @change="articleForm.cover.images=[]" >
            <el-radio :label="1"></el-radio>
            <el-radio :label="3"></el-radio>
            <el-radio :label="0"></el-radio>
            <el-radio :label="-1"></el-radio>
          </el-radio-group>
          <!--  -->
          <!--  -->
          <div style="margin-top:20px"
            v-if="articleForm.cover.type===1">
            <!-- :value="testImgUrl" @input="testImgUrl = $event" -->
            <!-- <my-image v-model="testImgUrl"></my-image> -->
            <my-image @confirm="checkCover" v-model="articleForm.cover.images[0]"></my-image>
          </div>
          <!--  -->
          <div style="margin-top:20px"
            v-if="articleForm.cover.type===3">
            <my-image @confirm="checkCover" v-for="i in 3" :key="i" v-model="articleForm.cover.images[i-1]"></my-image>
          </div>
        </el-form-item>
        <el-form-item label=": " prop="channel_id">
          <!--  -->
          <my-channel v-model="articleForm.channel_id"></my-channel>
        </el-form-item>
        <!-- id -->
        <el-form-item v-if="$route.query.id">
          <el-button @click="editArticle" type="success"></el-button>
        </el-form-item>
        <el-form-item v-else>
          <el-button @click="saveArticle(false)" type="primary"></el-button>
          <el-button @click="saveArticle(true)"></el-button>
        </el-form-item>
      </el-form>
    </el-card>
    <!--  -->
    <!-- :mySync="testSync" @update:mySync="testSync=$event":mySync.sync="testSync" 
    @update: -->
    <!-- <my-prop-sync :mySync.sync="testSync"></my-prop-sync> -->
  </div>
</template>

<script>
// 
import 'quill/dist/quill.core.css'
import 'quill/dist/quill.snow.css'
import 'quill/dist/quill.bubble.css'

import { quillEditor } from 'vue-quill-editor'

// sync
// import MyPropSync from '@/components/my-prop-sync'

export default {
    name:'page-publish',
    //  // sync
    components: {quillEditor/*,MyPropSync*/},
   
    data() {
      // 
      const validCoverFn = (rule,value,callback) =>{
        // value1 3 0 -1
        const images = this.articleForm.cover.images
        // 
        if(value === 1) {
          // ,1
          if(images[0] && images.length === 1) {
            callback()
          }else {
            callback(new Error(''))
          }
        }else if(value ===3) {
          // 
          // 
          if(images[0] && images[1] && images[2]) {
            callback()
          }else {
            callback(new Error(''))
          }
        }else {
          callback()
        }
      }
      return {
        // 
        // testImgUrl:null,
        // sync
        // testSync:"sync",
        // 
        articleForm:{
          title:null,
          // id
          channel_id:null,
          // 
          content:null,
          // 
          cover:{
            // 
            type:1,
            // 
            images:[]
          }
        },
        // 
        articleRules:{
          title:[
            { required: true, message: '', trigger: 'blur' },
            { min: 5, max: 30, message: ' 5  30 ', trigger: 'blur' }
          ],
          channel_id:[
            { required: true, message: '', trigger: 'change' },
          ],
          content:[
            { required: true, message: '', trigger: 'blur' },
          ],
          // articleFrom.cover.typechange
          'cover.type':[
            {validator:validCoverFn,trigger: 'change'}
          ]
        },
        // 
        editorOption:{
          modules: {
            toolbar: [
              // 
              ['bold', 'italic', 'underline', 'strike'],  
              ['blockquote', 'code-block'],
              // 
              [{ 'header': 1 }, { 'header': 2 }], 
              [{ 'list': 'ordered'}, { 'list': 'bullet' }],
              // /
              [{ 'indent': '-1'}, { 'indent': '+1' }], 
              ['image']
            ]
          },
          placeholder:''
        }
      }
    },
    created(){
      // 
      // 
      // 
      // 1.
      if(this.$route.query.id) {
        this.getArticle()
      }
    },
    methods:{
      // 
      checkContemt() {
        // contentcontent
        // validateField('')
        this.$refs.articleForm.validateField('content')
      },
      // 
      checkCover() {
        // cover.type
        this.$refs.articleForm.validateField('cover.type')
      },
      // 
      saveArticle(draft) {
        // 
        this.$refs.articleForm.validate(async valid => {
          if(valid) {
            // 
            try {
              // 
              await this.$http.post(`articles?draft=${draft}`,this.articleForm)
              this.$message.success(draft ? '' :'')
              // 
              this.$router.push('/article')
            } catch (error) {
              this.$message.error(draft ? '' :'')
            }
          }
        })
      },
      // 
      async getArticle() {
        const res = await this.$http.get(`articles/${this.$route.query.id}`)
        this.articleForm = res.data.data
      },
      // 
      editArticle() {
        // 
        this.$refs.articleForm.validate(async valid => {
          if(valid) {
            // 
            try {
              // 
              await this.$http.put(`articles/${this.$route.query.id}?draft=false`,this.articleForm)
              this.$message.success('')
              // 
              this.$router.push('/article')
            } catch (error) {
              this.$message.error('')
            }
          }
        })
      }
    },
    // 
    watch:{
      // id
      '$route.query.id':function() {
        if(this.$route.query.id) {
          //  + 
          this.getArticle()
        }else {
          //  + 
          // resetFields
          this.$refs.articleForm.resetFields()
          //element
          this.articleForm.cover.images = []
        }
      }
    }
}
</script>

<style lang="less" scoped>
// ::v-deep
::v-deep .ql-toolbar.ql-snow {
  padding: 0 8px;
}
::v-deep .ql-editor {
  height: 250px;
}
</style>